import React from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Checkbox,
} from '@mui/material';
import { styled } from '@mui/material/styles';
import MDBox from 'components/MDBox';
import MDTypography from 'components/MDTypography';

// Styled components for consistent table styling
const StyledTableContainer = styled(TableContainer)(({ theme }) => ({
  borderRadius: theme.spacing(2),
  overflow: 'hidden',
  boxShadow: 'none',
  border: `1px solid ${theme.palette.grey[200]}`,
}));

const StyledTableHead = styled(TableHead)(({ theme }) => ({
  backgroundColor: theme.palette.grey[50],
}));

const StyledTableCell = styled(TableCell)(({ theme, width }) => ({
  padding: theme.spacing(2),
  fontSize: '0.875rem',
  fontWeight: 500,
  borderBottom: `1px solid ${theme.palette.grey[200]}`,
  whiteSpace: 'nowrap',
  overflow: 'hidden',
  textOverflow: 'ellipsis',
  width: width || 'auto',
  maxWidth: width ? `${width}px` : 'none',
  verticalAlign: 'middle',
  height: 'auto',
  minHeight: '56px',
  
  '&.MuiTableCell-head': {
    backgroundColor: theme.palette.grey[50],
    fontWeight: 600,
    color: theme.palette.text.primary,
    textTransform: 'uppercase',
    fontSize: '0.75rem',
    letterSpacing: '0.5px',
    verticalAlign: 'middle',
    height: '56px',
  },
  
  '&.MuiTableCell-body': {
    verticalAlign: 'middle',
    height: 'auto',
    minHeight: '56px',
  },
  
  // Ensure all content within cells is vertically centered
  '& > *': {
    display: 'flex',
    alignItems: 'center',
    minHeight: '40px',
  },
  
  // Special handling for checkboxes
  '&.MuiTableCell-paddingCheckbox': {
    '& > *': {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:hover': {
    backgroundColor: theme.palette.action.hover,
  },
  '&:last-child td': {
    borderBottom: 0,
  },
}));

const DataCell = styled(MDBox)({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'inherit',
  maxWidth: '100%',
  overflow: 'hidden',
  minHeight: '48px',
  width: '100%',
  
  // Ensure nested content aligns properly
  '& .MuiChip-root': {
    margin: '2px 0',
  },
  
  '& .MuiIconButton-root': {
    margin: '0',
    padding: '8px',
  },
  
  '& .MuiAvatar-root': {
    margin: '2px 0',
  },
  
  // Handle multi-line content
  '& .MuiTypography-root': {
    lineHeight: '1.4',
    display: 'flex',
    alignItems: 'center',
  },
});

const TruncatedText = styled(MDTypography)(({ maxWidth = 200 }) => ({
  maxWidth: `${maxWidth}px`,
  overflow: 'hidden',
  textOverflow: 'ellipsis',
  whiteSpace: 'nowrap',
}));

// Enhanced Table Component
function EnhancedTable({
  columns = [],
  data = [],
  selectedItems = [],
  onSelectAll,
  onSelectItem,
  showCheckbox = false,
  stickyHeader = false,
  maxHeight = 'auto',
  responsive = true,
}) {
  const allSelected = data.length > 0 && selectedItems.length === data.length;
  const indeterminate = selectedItems.length > 0 && selectedItems.length < data.length;

  const handleSelectAll = (event) => {
    if (onSelectAll) {
      onSelectAll(event.target.checked);
    }
  };

  const handleSelectItem = (item) => (event) => {
    if (onSelectItem) {
      onSelectItem(item, event.target.checked);
    }
  };

  return (
    <StyledTableContainer component={Paper} sx={{ maxHeight, overflow: 'auto' }}>
      <Table stickyHeader={stickyHeader} size="medium">
        <StyledTableHead>
          <TableRow>
            {showCheckbox && (
              <StyledTableCell padding="checkbox" width={50}>
                <Checkbox
                  indeterminate={indeterminate}
                  checked={allSelected}
                  onChange={handleSelectAll}
                  color="primary"
                />
              </StyledTableCell>
            )}
            {columns.map((column) => (
              <StyledTableCell
                key={column.id}
                align={column.align || 'left'}
                width={column.width}
                sx={{ minWidth: column.minWidth || 'auto' }}
              >
                {column.label}
              </StyledTableCell>
            ))}
          </TableRow>
        </StyledTableHead>
        <TableBody>
          {data.map((row, index) => (
            <StyledTableRow key={row.id || index}>
              {showCheckbox && (
                <StyledTableCell padding="checkbox">
                  <Checkbox
                    checked={selectedItems.some(item => item.id === row.id)}
                    onChange={handleSelectItem(row)}
                    color="primary"
                  />
                </StyledTableCell>
              )}
              {columns.map((column) => (
                <StyledTableCell
                  key={column.id}
                  align={column.align || 'left'}
                  width={column.width}
                >
                  <DataCell>
                    {column.format ? column.format(row[column.id], row) : (
                      <TruncatedText
                        variant="body2"
                        maxWidth={column.maxWidth || 200}
                        title={String(row[column.id])}
                      >
                        {row[column.id]}
                      </TruncatedText>
                    )}
                  </DataCell>
                </StyledTableCell>
              ))}
            </StyledTableRow>
          ))}
          {data.length === 0 && (
            <StyledTableRow>
              <StyledTableCell colSpan={columns.length + (showCheckbox ? 1 : 0)} align="center">
                <MDBox py={4}>
                  <MDTypography variant="body2" color="text" opacity={0.7}>
                    No data available
                  </MDTypography>
                </MDBox>
              </StyledTableCell>
            </StyledTableRow>
          )}
        </TableBody>
      </Table>
    </StyledTableContainer>
  );
}

export default EnhancedTable;
